import {asyncExec} from './utils';

export function init(): Promise<void> {
    return asyncExec('HMSLocationKit', 'init', []);
}

export function disableLogger(): Promise<void> {
    return asyncExec('HMSLocationKit', 'disableLogger', []);
}

export function enableLogger(): Promise<void> {
    return asyncExec('HMSLocationKit', 'enableLogger', []);
}
